import { createSlice, isAnyOf } from '@reduxjs/toolkit';
import {
  fetchContacts,
  addContact,
  deleteContact,
  toggleIsFavourite,
} from 'redux/operation';

// !  Redux Toolkit
///   
const initialState = {
  //   
  contacts: {
    items: [], //  
    isLoading: false, //  
    error: null, //  ( )
    isFavourite: false, //     -   
    favouriteContacts: [], //   
  },
  filterWord: '', //    
};

const contactsSlice = createSlice({
  name: 'contacts',
  initialState,
  reducers: {
    setFilterWord(state, action) {
      state.filterWord = action.payload;
    },
  },
  extraReducers: builder => {
    builder
      //   
      .addCase(fetchContacts.fulfilled, (state, action) => {
        state.contacts.isLoading = false;
        //      
        state.contacts.items = action.payload;
        state.contacts.error = null;
      })
      .addCase(addContact.fulfilled, (state, action) => {
        state.contacts.isLoading = false;
        state.contacts.items = [...state.contacts.items, action.payload];
      })
      .addCase(deleteContact.fulfilled, (state, action) => {
        state.contacts.isLoading = false;
        state.contacts.error = null;
        state.contacts.items = state.contacts.items.filter(
          item => item.id !== action.payload.id
        );
        // const index = state.contacts.items.findIndex(
        //   contact => [contact.id] === [action.payload.id]
        // );
        // state.contacts.items.splice(index, 1);
      })
      .addCase(toggleIsFavourite.fulfilled, (state, action) => {
        state.contacts.isLoading = false;
        state.contacts.error = null;
        //  isFavourite    id
        state.contacts.items = state.contacts.items.map(contact => {
          if (contact.id === action.payload.id) {
            return {
              ...contact,
              isFavourite: !contact.isFavourite, //  isFavourite
            };
          }
          return contact;
        });
      })

      //    fetchContacts  fetchAddContact
      .addMatcher(
        isAnyOf(
          fetchContacts.pending,
          addContact.pending,
          deleteContact.pending,
          toggleIsFavourite.pending
        ),
        state => {
          state.contacts.isLoading = true;
          state.contacts.error = null;
        }
      )
      //    fetchContacts  fetchAddContact
      .addMatcher(
        isAnyOf(
          fetchContacts.rejected,
          addContact.rejected,
          deleteContact.rejected,
          toggleIsFavourite.rejected
        ),
        (state, { payload }) => {
          state.contacts.isLoading = false;
          state.contacts.error = payload;
        }
      );
  },
});
export const { setFilterWord } = contactsSlice.actions;

//    
export const contactsReducer = contactsSlice.reducer;
